@font-face {
  font-family: 'Clacon';
  src: local('Clacon'), url(./fonts/clacon.ttf) format('truetype');
}

@font-face {
  font-family: 'Glass';
  src: local('Glass'), url(./fonts/Glass_TTY_VT220.ttf) format('truetype');
}

body {
  background-color: #62abff;
  padding: 20px;
  margin: 0;
}
h1, h2, p, ul, li {
  font-family: 'Times New Roman', Times, serif
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  background-color: #111;
  padding: 0;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}

