.section .type{
  position: relative;
  top: 50%;  
  width: 24em;
  margin: 0 auto;
  border-right: 2px solid rgba(255,255,255,.75);
  font-size: 180%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  color: #00ff00;
  font-family: 'Courier New','Courier','Lucida Sans Typewriter','Lucida Typewriter',monospace; 
}
.anim-typewriter{

  -webkit-animation: typewriter 4s steps(44) 1s 1 normal both,
             blinkTextCursor 500ms steps(44) 11 normal;

          animation: typewriter 4s steps(44) 1s 1 normal both,
             blinkTextCursor 500ms steps(44) 11 normal;
  -webkit-animation-fill-mode: both,forwards;
          animation-fill-mode: both,forwards;
}

.anim-typewriter-two{
  -webkit-animation: typewriter2 3s steps(44) 1s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
          animation: typewriter2 3s steps(44) 1s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}
@-webkit-keyframes typewriter{
  from{width: 0; visibility: hidden;}
  to{width: 28em; visibility: visible;}
}
@keyframes typewriter{
  from{width: 0; visibility: hidden;}
  to{width: 28em; visibility: visible;}
}
@-webkit-keyframes typewriter2{
  from{width: 0; visibility: hidden;}
  to{width: 13em; visibility: visible;}
}
@keyframes typewriter2{
  from{width: 0; visibility: hidden;}
  to{width: 13em; visibility: visible;}
}
@-webkit-keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent; }
  
}
@keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent; }
  
}

